/* eslint-disable @typescript-eslint/no-explicit-any */
import { css, SerializedStyles } from '@emotion/react';
import { AtomWrapper } from '@ixulabs/ui';
import { AnimatePresence } from 'framer-motion';
import { FC, MutableRefObject, ReactNode, useEffect, useState } from 'react';

type Props = {
  children?: ReactNode;
  refObject: MutableRefObject<any>;
  minRestoffsetTop?: number;
  customCSSW?: SerializedStyles;
};

const AtomWrapperScroll: FC<Props> = ({
  refObject,
  children,
  minRestoffsetTop,
  customCSSW,
}) => {
  const [scroll, setScroll] = useState(false);

  useEffect(() => {
    const getScroll = () => {
      const rest = minRestoffsetTop ?? 40;
      setScroll(window.scrollY > (refObject?.current?.offsetTop || 0) - rest);
    };

    const watchScroll = () => {
      window.addEventListener('scroll', getScroll, true);
    };

    watchScroll();
    return () => {
      window.removeEventListener('scroll', getScroll, true);
    };
  });
  return scroll ? (
    <AnimatePresence>
      <AtomWrapper
        customCSS={css`
          display: flex;
          flex-direction: row;
          position: fixed;
          z-index: 2;
          bottom: 10vh;
          right: 5vw;
          width: max-content;
          ${customCSSW ?? css``}
        `}
      >
        {children}
      </AtomWrapper>
    </AnimatePresence>
  ) : null;
};

export default AtomWrapperScroll;
